const mathStr1 = `We incorporate control variates constructed by Stein's identity into the score distillation and
derive Stein Score Distillation (SSD):

$$
\\mathbb{E}_{t, c, \\epsilon \\sim \\mathcal{N}(0, I)} \\left[\\omega(t) \\frac{\\partial x_t}{\\partial \\theta} \\left(\\nabla \\log p_t(x_t | y) + \\epsilon \\phi(t, x_t, \\theta, c) + \\nabla_x \\phi(t, x_t, \\theta, c) \\right) \\right]
$$

$$
x_t=\\alpha_t \\mathrm{NeRF}_{r g b}(\\theta, c)+\\sigma_t \\epsilon
$$

The control variate allows for arbitrary differnetiable baseline functions dependent on
timestep, NeRF parameters, noisy rendered image, and camera pose. For instance, we
implement the baseline function with an MiDAS depth estimator in this paper.

$$
\\phi(t, x_t, \\theta, c) = 1 - \\mathrm{PearsonCorr}(\\mathrm{NeRF}_{depth}(\\theta, c), \\text{MiDAS}(x_t))
$$
`

const mathStr2 = `
Score distillation essentially updates NeRF via a huge expectation:

$$
\\mathbb{E}_{t, c, \\epsilon \\sim \\mathcal{N}(0, I)} \\left[\\omega(t) \\frac{\\partial x_t}{\\partial \\theta} \\nabla \\log p_t(x_t | y) \\right]
$$

Evaluating this expectation via Monte-Carlo estimation suffers from high variance. This
motivates us to leverage control variate methods to reduce variance.

Wait, what is control variate method? Imagine you want to estimate $\\mathbb{E}[f(X)]$. You can instead estimate $\\mathbb{E}[f(X) + h(X)]$ in which $\\mathbb{E}[h(X)] = 0$ so that the mean is unchanged but the variance can be potentially reduced.

To construct a generic control variate, we find Stein identity can be an ideal tool. Here $\\phi(x)$ is called baseline function and can be literally any function.

$$
\\mathbb{E}_{x \\sim p} [\\nabla \\log p(x) \\phi(x) + \\nabla_x \\phi(x)] = 0
$$

By plugging Stein identity into the original expectation, we can easily obtain our SSD.
`

